"use client";

import React, { useState } from "react";
import { GoogleGenerativeAI } from "@google/generative-ai";
import {
  Radar, RadarChart, PolarGrid, PolarAngleAxis, PolarRadiusAxis, ResponsiveContainer, Tooltip
} from "recharts";
import { motion } from "framer-motion";
import { 
  Loader2, Zap, Brain, Activity, Users, Moon, ArrowUpRight, TrendingUp, CheckCircle2 
} from "lucide-react";

// ⚠️ API KEY HERE
const API_KEY = "AIzaSy...PUT_YOUR_KEY..."; 
const genAI = new GoogleGenerativeAI(API_KEY);
const model = genAI.getGenerativeModel({ model: "gemini-1.5-flash" });

export default function Dashboard() {
  const [input, setInput] = useState("");
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState<any>(null);

  const analyze = async () => {
    if (!input.trim()) return;
    setLoading(true);
    
    try {
      // Prompt مُفصل باش يخرج لينا الداتا بـ Structure اللي بغينا
      const prompt = `
        Act as a high-performance coach. Analyze this daily journal: "${input}". 
        Return strictly JSON with no markdown:
        {
          "summary": "One concise sentence summarizing the day's performance.",
          "metrics": [
            {"subject": "Focus", "A": 0-100, "trend": "up/down/neutral", "desc": "Brief reason"},
            {"subject": "Speed", "A": 0-100, "trend": "up/down/neutral", "desc": "Brief reason"},
            {"subject": "Clarity", "A": 0-100, "trend": "up/down/neutral", "desc": "Brief reason"},
            {"subject": "Social", "A": 0-100, "trend": "up/down/neutral", "desc": "Brief reason"},
            {"subject": "Rest", "A": 0-100, "trend": "up/down/neutral", "desc": "Brief reason"}
          ],
          "strategic_insight": "One powerful, actionable recommendation for tomorrow.",
          "improvements": ["Short suggestion 1", "Short suggestion 2", "Short suggestion 3"]
        }
      `;
      
      const res = await model.generateContent(prompt);
      const text = res.response.text().replace(/```json|```/g, "").trim();
      setData(JSON.parse(text));
    } catch (error) {
      console.error(error);
      alert("System overload. Try again.");
    } finally {
      setLoading(false);
    }
  };

  // Helper icons mapping
  const getIcon = (subject: string) => {
    switch(subject) {
      case "Focus": return <Zap className="w-4 h-4 text-amber-400" />;
      case "Speed": return <TrendingUp className="w-4 h-4 text-blue-400" />;
      case "Clarity": return <Brain className="w-4 h-4 text-purple-400" />;
      case "Social": return <Users className="w-4 h-4 text-green-400" />;
      case "Rest": return <Moon className="w-4 h-4 text-indigo-400" />;
      default: return <Activity className="w-4 h-4 text-white" />;
    }
  };

  // --- 1. INPUT MODE VIEW ---
  if (!data && !loading) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen p-6 max-w-2xl mx-auto w-full">
        <div className="w-full space-y-6">
          <div className="space-y-2 text-center mb-10">
            <h2 className="text-2xl font-semibold text-white tracking-tight">Daily Synchronization</h2>
            <p className="text-neutral-500">Log your activities, thoughts, and outcomes.</p>
          </div>
          
          <div className="relative group">
            <div className="absolute -inset-0.5 bg-gradient-to-r from-neutral-700 to-neutral-800 rounded-xl blur opacity-30 group-hover:opacity-100 transition duration-1000"></div>
            <textarea
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder="E.g., 08:00 Deep work session. 12:00 Lunch with team. 15:00 Felt tired but pushed through..."
              className="relative w-full h-64 bg-[#0a0a0a] border border-neutral-800 rounded-xl p-6 text-base text-neutral-200 placeholder:text-neutral-700 focus:outline-none focus:border-neutral-600 focus:ring-1 focus:ring-neutral-600 transition-all resize-none"
            />
          </div>

          <button
            onClick={analyze}
            disabled={!input}
            className="w-full py-4 bg-white text-black font-semibold rounded-xl hover:bg-neutral-200 transition-all flex justify-center items-center gap-2 disabled:opacity-50"
          >
            Initialize Analysis <ArrowUpRight className="w-4 h-4" />
          </button>
        </div>
      </div>
    );
  }

  // --- 2. LOADING VIEW ---
  if (loading) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen gap-4">
        <Loader2 className="w-8 h-8 animate-spin text-white" />
        <p className="text-neutral-500 font-mono text-sm animate-pulse">PROCESSING_NEURAL_DATA...</p>
      </div>
    );
  }

  // --- 3. DASHBOARD VIEW (Bento Grid) ---
  return (
    <div className="min-h-screen p-6 md:p-10 max-w-[1400px] mx-auto">
      
      {/* Header */}
      <header className="flex justify-between items-end mb-10 border-b border-neutral-800 pb-6">
        <div>
          <div className="flex items-center gap-2 mb-1">
            <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse" />
            <span className="text-xs font-mono text-green-500 uppercase tracking-widest">Analysis Complete</span>
          </div>
          <h1 className="text-3xl font-bold text-white tracking-tight">Today's Overview</h1>
          <p className="text-neutral-500 mt-1">{data.summary}</p>
        </div>
        <div className="text-right hidden md:block">
          <p className="text-xs text-neutral-600 font-mono">LIFE_OS V2.0</p>
          <p className="text-neutral-400 font-medium">{new Date().toLocaleDateString('en-US', { weekday: 'long', month: 'long', day: 'numeric' })}</p>
        </div>
      </header>

      {/* Main Grid */}
      <div className="grid grid-cols-1 md:grid-cols-12 gap-6">
        
        {/* Radar Chart (Left - 4 Cols) */}
        <motion.div 
          initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }}
          className="md:col-span-4 bento-card rounded-2xl p-6 flex flex-col justify-between min-h-[400px]"
        >
          <h3 className="text-sm font-medium text-neutral-400 uppercase tracking-wider mb-4">Balance Radar</h3>
          <div className="flex-1 -ml-6">
            <ResponsiveContainer width="100%" height="100%">
              <RadarChart cx="50%" cy="50%" outerRadius="70%" data={data.metrics}>
                <PolarGrid stroke="#333" strokeDasharray="3 3" />
                <PolarAngleAxis dataKey="subject" tick={{ fill: "#737373", fontSize: 11, fontWeight: 600 }} />
                <PolarRadiusAxis angle={30} domain={[0, 100]} tick={false} axisLine={false} />
                <Tooltip 
                   contentStyle={{ backgroundColor: '#0a0a0a', borderColor: '#333', color: '#fff', fontSize: '12px' }} 
                   itemStyle={{ color: '#fff' }}
                />
                <Radar name="Today" dataKey="A" stroke="#fff" strokeWidth={2} fill="#fff" fillOpacity={0.1} />
              </RadarChart>
            </ResponsiveContainer>
          </div>
        </motion.div>

        {/* Metrics Cards (Right - 8 Cols) */}
        <div className="md:col-span-8 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
          {data.metrics.map((m: any, i: number) => (
            <motion.div 
              key={i} 
              initial={{ opacity: 0, scale: 0.95 }} animate={{ opacity: 1, scale: 1 }} transition={{ delay: i * 0.1 }}
              className="bento-card p-5 rounded-xl flex flex-col justify-between hover:bg-neutral-900/50"
            >
              <div className="flex justify-between items-start mb-4">
                <div className="flex items-center gap-2 text-neutral-300 font-medium">
                  {getIcon(m.subject)} {m.subject}
                </div>
                <span className="text-2xl font-bold text-white">{m.A}</span>
              </div>
              <p className="text-xs text-neutral-500 leading-relaxed border-t border-white/5 pt-3 mt-auto">
                {m.desc}
              </p>
            </motion.div>
          ))}
          
          {/* Strategic Insight (Span Full Width of metrics) */}
          <div className="sm:col-span-2 lg:col-span-3 bento-card p-6 rounded-xl bg-gradient-to-br from-neutral-900 to-black border-l-4 border-l-white">
            <div className="flex items-center gap-2 mb-3">
              <Brain className="w-5 h-5 text-white" />
              <h3 className="text-sm font-bold text-white uppercase tracking-wider">Strategic Recommendation</h3>
            </div>
            <p className="text-lg text-neutral-200 leading-relaxed">
              "{data.strategic_insight}"
            </p>
          </div>
        </div>

        {/* Bottom Section: Improvements & Original Journal */}
        <div className="md:col-span-4 bento-card p-6 rounded-2xl">
          <h3 className="text-sm font-medium text-neutral-400 uppercase tracking-wider mb-4">Optimization Protocol</h3>
          <ul className="space-y-3">
            {data.improvements.map((imp: string, i: number) => (
              <li key={i} className="flex items-start gap-3 text-sm text-neutral-300">
                <CheckCircle2 className="w-4 h-4 text-neutral-600 mt-0.5 shrink-0" />
                {imp}
              </li>
            ))}
          </ul>
        </div>

        <div className="md:col-span-8 bento-card p-6 rounded-2xl opacity-70 hover:opacity-100 transition-opacity">
           <h3 className="text-sm font-medium text-neutral-500 uppercase tracking-wider mb-2">Source Journal</h3>
           <p className="text-sm font-mono text-neutral-400 leading-relaxed line-clamp-4 hover:line-clamp-none transition-all">
             {input}
           </p>
        </div>

      </div>
    </div>
  );
}